.modal-map {
  position: fixed;
  right: 0;
  left: 0;
  top: 100px;
  margin: 0 auto;
  width: 960px;
  height: 573px;
  box-shadow: 0 10px 16px rgba(0, 0, 0, 0.15);
  background: url("../img/index/map-big.png") no-repeat;
  background-size: cover;
  z-index: 10;
}
@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes shake {
  0%,
  to {
    transform: translateX(0);
  }
  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(-10px);
  }
  20%,
  40%,
  60%,
  80% {
    transform: translateX(10px);
  }
}
.feedback {
  position: fixed;
  right: 0;
  left: 0;
  top: 100px;
  margin: 0 auto;
  width: 960px;
  min-height: 550px;
  box-shadow: 0 10px 16px rgba(0, 0, 0, 0.15);
  z-index: 10;
  background-color: #ffffff;
  box-sizing: border-box;
  padding: 92px 100px 62px;
}
.feedback__wrap {
  display: grid;
  grid-template-columns: 360px 360px;
  grid-column-gap: 40px;
  grid-row-gap: 30px;
}
